<template lang="pug">
div
	.factorian-content-block.factorian-breadcroumb.section-gray
		.container
				.row
					.col-md-6
						h2 El Canal Para Vernos

	.factorian-content-block
		.container
				.row
					.col-md-12
						h1 Programación

				.row
					#exTab1.col-md-12
						ul.nav.nav-pills
							li.active
								a(href="#1a" data-toggle="tab") Lunes
							li
								a(href="#2a" data-toggle="tab") Martes
							li
								a(href="#3a" data-toggle="tab") Miercoles
							li
								a(href="#4a" data-toggle="tab") Jueves
							li
								a(href="#5a" data-toggle="tab") Viernes
							li
								a(href="#6a" data-toggle="tab") Sabado
							li
								a(href="#7a" data-toggle="tab") Domingo

						.tab-content.clearfix
							.tab-pane.active(id="1a")
								box-programa(tab="Lunes" :programas="Global.programacion.lunes" v-if="Global.programacion.lunes.length > 0")

							.tab-pane(id="2a")
								box-programa(tab="Martes" :programas="Global.programacion.martes" v-if="Global.programacion.martes.length > 0")

							.tab-pane(id="3a")
								box-programa(tab="Miercoles" :programas="Global.programacion.miercoles" v-if="Global.programacion.miercoles.length > 0")

							.tab-pane(id="4a")
								box-programa(tab="Jueves" :programas="Global.programacion.jueves" v-if="Global.programacion.jueves.length > 0")

							.tab-pane(id="5a")
								box-programa(tab="Viernes" :programas="Global.programacion.viernes" v-if="Global.programacion.viernes.length > 0")

							.tab-pane(id="6a")
								box-programa(tab="Sabado" :programas="Global.programacion.sabado" v-if="Global.programacion.sabado.length > 0")

							.tab-pane(id="7a")
								box-programa(tab="Domingo" :programas="Global.programacion.domingo" v-if="Global.programacion.domingo.length > 0")
								
</template>

<script>
import { getIdFromURL } from 'vue-youtube-embed'
import BoxPrograma from './../components/boxPrograma.vue'
import { mapState } from 'vuex'
export default {
	components: {
      BoxPrograma
   },
   computed: {
      ... mapState(['Global']) 
   },
	created () {
		this.$store.dispatch('Global/GET_PROGRAMACION', 1)
		this.$store.dispatch('Global/GET_PROGRAMACION', 2)
		this.$store.dispatch('Global/GET_PROGRAMACION', 3)
		this.$store.dispatch('Global/GET_PROGRAMACION', 4)
		this.$store.dispatch('Global/GET_PROGRAMACION', 5)
		this.$store.dispatch('Global/GET_PROGRAMACION', 6)
		this.$store.dispatch('Global/GET_PROGRAMACION', 7)
	},
   methods: {
      getId (url) {
         return 'https://www.youtube.com/embed/' + this.$youtube.getIdFromURL(url) + '?showinfo=0'
      }
   }
}

</script>

<style>
.factorian-content-block {
    padding: 22px 0;
}
</style>


