<template lang="pug">
div
   h3 {{ tab}}
   .row
      .col-sm-3(v-for="item in programas")
         
         .single-service-item.wow.fadeInUp
            .foto(:style="{background:'url(' + Global.url +'uploads/post/' + item.programa.imagen + ')'}")

            p {{ item.programa.titulo }}
            p {{ item.hora.date | moment("HH:mm") }} hs
            p {{Global.url}}


</template>

<script>
import { mapState } from 'vuex'
export default {
   props: ['programas', 'tab'],
   computed: {
      ... mapState(['Global']) 
   },
}

</script>


